/* Register.css */
.App-Register-Row {
    display: flex;
    justify-content: space-around;
    padding: 20px;
  }
  
  .error {
    color: red;
    font-weight: bold;
  }
  
  input {
    padding: 8px;
    margin: 5px 0;
    width: 100%;
    box-sizing: border-box;
  }
  
  button {
    padding: 8px 16px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  li button {
    background-color: #ff4d4d;
  }
  
  li button:hover {
    background-color: #cc0000;
  }